import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { Card } from "../components/AuthForms";
import { useAuth } from "../context/auth";
import { makeStyles } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import { Snackbar, TextField, Button } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
    link: {
        margin: theme.spacing(2),
        color: '#3f51b5',
        textDecoration: "unset",
    },
    form: {
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
    },
    input: {
        marginBottom: theme.spacing(2),
    },
    icon: {
        margin: theme.spacing(5),
        fontSize: '120px'
    }
}));

function MuiAlert(props) {
    return <Alert elevation={6} variant="filled" {...props} />;
}

function Login(props) {
    const classes = useStyles();
    const [isLoggedIn, setLoggedIn] = useState(false);
    const [isError, setIsError] = useState(false);
    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");
    const [openNotify, setOpenNotify] = React.useState(false);
    const { setAuthTokens } = useAuth();
    const referer = props.location.state.referer || '/';

    const handleCloseNotify = (event, reason) => {
        if (reason === 'clickaway') return;
        setOpenNotify(false);
    };

  function postLogin() {
    if (userName === 'user' && password === 'user') {
        setAuthTokens('bdojwfbre3hur3348rh3984rh3048rh439');
        setLoggedIn(true);
    } else if (userName === '' && password === '') {
        setIsError(true);
        setOpenNotify(true);
    }
    else {
        setIsError(true);
        setOpenNotify(true);
    }
    // axios.post("https://www.somePlace.com/auth/login", {
    //   userName,
    //   password
    // }).then(result => {
    //   if (result.status === 200) {
    //     setAuthTokens(result.data);
    //     setLoggedIn(true);
    //   } else {
    //     setIsError(true);
    //   }
    // }).catch(e => {
    //   setIsError(true);
    // });
  }

  if (isLoggedIn) {
    return <Redirect to={referer} />;
  }

  return (
        <Card>
        <AccountCircleIcon className={classes.icon} fontSize="large" color="primary" />
          <form className={classes.form}>
              <TextField
                    label="Email"
                    variant="outlined"
                    type="username"
                    value={userName}
                    onChange={e => {
                    setUserName(e.target.value);
                    }}
                    className={classes.input}
              />
              <TextField
                  label="Password"
                  variant="outlined"
                  type="password"
                  value={password}
                  onChange={e => {
                      setPassword(e.target.value);
                  }}
                  className={classes.input}
              />
            <Button size="large" color="primary" variant="contained" onClick={postLogin}>Sign In</Button>
          </form>
          <Link className={classes.link} to="/signup">Don't have an account?</Link>
            {isError &&
                <Snackbar open={openNotify} autoHideDuration={1000} onClose={handleCloseNotify}>
                    <MuiAlert severity="error" variant="filled" onClose={handleCloseNotify}>
                        The username or password provided were incorrect!
                    </MuiAlert>
                </Snackbar>
            }
    </Card>
  );
}

export default Login;