import React, { useState } from "react";
import { BrowserRouter as Router, Link, Route } from "react-router-dom";
import PrivateRoute from './PrivateRoute';
import Home from "./pages/Home";
import Page from "./pages/Page";
import { AuthContext } from "./context/auth";
import Login from "./pages/Login";
import Signup from './pages/Signup';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  link: {
    color: '#fff',
    textDecoration: 'none',
  }
}));

function App(props) {
  const classes = useStyles();

  const [authTokens, setAuthTokens] = useState();
  
  const setTokens = (data) => {
    localStorage.setItem("tokens", JSON.stringify(data));
    setAuthTokens(data);
  }

  return (
    <AuthContext.Provider value={{ authTokens, setAuthTokens: setTokens }}>
      <Router>
        <div>
          <AppBar position="static">
            <Toolbar>
              <Button color="inherit"><Link className={classes.link} to="/">Home Page</Link></Button>
              { !authTokens && <Button color="inherit"><Link className={classes.link} to="/admin">Login</Link></Button> }
              { authTokens && <Button color="inherit" onClick={() => {setAuthTokens()}}>Log Out</Button> }
            </Toolbar>
          </AppBar>
          <Route exact path="*" component={Home} />
          <Route path="/login" component={Login} />
          <Route path="/signup" component={Signup} />
          <PrivateRoute path="/admin" component={Page} />
        </div>
      </Router>
    </AuthContext.Provider>
  );
}

export default App;